 import React, { useState } from 'react';
import { Routes, Route, Link, useLocation, Navigate } from 'react-router-dom';
import RegistroUsuario from './RegistroUsuario';
import Produccion from './Produccion';
import OrdenesTrabajo from './OrdenesTrabajo';
import NuevaOT from './NuevaOT';
import Auth from './Auth';



import {
  Box, Drawer, List, ListItem, ListItemIcon, ListItemText, Toolbar,
  AppBar, Typography, CssBaseline, IconButton, Container
} from '@mui/material';

import GroupAdd from '@mui/icons-material/GroupAdd';
import PrecisionManufacturing from '@mui/icons-material/PrecisionManufacturing';
import BorderColor from '@mui/icons-material/BorderColor';
import Grading from '@mui/icons-material/Grading';
import LogoutIcon from '@mui/icons-material/Logout';
import Brightness4 from '@mui/icons-material/Brightness4';
import Brightness7 from '@mui/icons-material/Brightness7';

const drawerWidth = 240;

const menuItems = [
  { text: 'Registro de Usuario', icon: <GroupAdd />, path: '/' },
  { text: 'Producción', icon: <PrecisionManufacturing />, path: '/produccion' },
  { text: 'Ordenes de Trabajo', icon: <BorderColor />, path: '/ordenes' },
  { text: 'Nueva OT', icon: <Grading />, path: '/nueva-ot' },
];



function App({ toggleTheme, mode }) {
  const location = useLocation();
  const [usuario, setUsuario] = useState(null);

  const handleLogout = () => {
    localStorage.removeItem('token');
    sessionStorage.removeItem('token');
    setUsuario(null);
  };

  if (!usuario) {
    return <Auth onAuthChange={setUsuario} />;
  }

  return (
      <Box sx={{ display: 'flex' }}>
      <CssBaseline />

      {/* AppBar superior */}
      <AppBar position="fixed" sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}>
        <Toolbar>
          <Typography variant="h6" noWrap component="div" sx={{ flexGrow: 1 }}>
            Mi Aplicación
          </Typography>
          {usuario && (
            <Typography variant="body1" sx={{ mr: 2 }}>
              Bienvenido, <strong>{usuario.nombre_completo || usuario.email}</strong>
            </Typography>
          )}
          <IconButton color="inherit" onClick={toggleTheme}>
            {mode === 'dark' ? <Brightness7 /> : <Brightness4 />}
          </IconButton>
          {usuario && (
            <IconButton color="inherit" onClick={handleLogout}>
              <LogoutIcon />
            </IconButton>
          )}
        </Toolbar>
      </AppBar>

      {/* Sidebar */}
      <Drawer
        variant="permanent"
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
            backgroundColor: '#1e1e2f',
            color: '#fff',
          },
        }}
      >
        <Toolbar />
        <List>
          {menuItems.map((item) => {
            const isActive = location.pathname === item.path;
            return (
              <ListItem
                button
                key={item.text}
                component={Link}
                to={item.path}
                sx={{
                  backgroundColor: isActive ? '#fff' : 'transparent',
                  color: isActive ? 'purple' : '#fff',
                  borderLeft: isActive ? '4px solid purple' : '4px solid transparent',
                  transition: 'all 0.3s ease-in-out',
                  '&:hover': {
                    backgroundColor: isActive ? '#fff' : '#333',
                  },
                }}
              >
                <ListItemIcon
                  sx={{
                    color: isActive ? 'purple' : '#fff',
                    transition: 'color 0.3s ease-in-out',
                  }}
                >
                  {item.icon}
                </ListItemIcon>
                <ListItemText primary={item.text} />
              </ListItem>
            );
          })}
        </List>
      </Drawer>

      {/* Contenido principal */}
      <Box
        component="main"
        sx={{ flexGrow: 1, p: 3, backgroundColor: 'background.default', minHeight: '100vh' }}
      >
        <Toolbar />
        <Container maxWidth="xl">
          <Routes>
            <Route path="/" element={<RegistroUsuario />} />
            <Route path="/produccion" element={<Produccion />} />
            <Route path="/ordenes" element={<OrdenesTrabajo usuario={usuario} />} />
            <Route path="/nueva-ot" element={<NuevaOT />} />
          </Routes>
        </Container>
      </Box>
    </Box>
  );
}

export default App;