import React, { useState, useEffect } from 'react';
import {
  Avatar, Button, TextField, FormControlLabel, Checkbox,
  Link, Paper, Box, Grid, Typography, Grow
} from '@mui/material';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { useNavigate } from 'react-router-dom';
import supabase from './supabaseClient';

function Auth({ onAuthChange }) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [nombreCompleto, setNombreCompleto] = useState('');
  const [isLogin, setIsLogin] = useState(true);
  const [rememberMe, setRememberMe] = useState(false);
  const [errorMsg, setErrorMsg] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrorMsg('');

    if (isLogin) {
      try {
        const res = await fetch('http://localhost:4000/api/login', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ email, password }),
        });

        const result = await res.json();

        if (!res.ok) {
          setErrorMsg(result.error);
        } else if (result.session?.access_token) {
          const token = result.session.access_token;
          if (rememberMe) {
            localStorage.setItem('token', token);
          } else {
            sessionStorage.setItem('token', token);
          }

          // Obtener nombre desde tabla Usuarios
          const { data: perfil } = await supabase
            .from('Usuarios')
            .select('Nombre')
            .eq('Id', result.user.id)
            .single();

          onAuthChange({
            ...result.user,
            nombre_completo: perfil?.Nombre || result.user.email,
          });

          navigate('/crear-ot');
        } else {
          setErrorMsg('No se recibió un token válido del servidor.');
        }
      } catch (err) {
        setErrorMsg('Error de red o del servidor');
      }
    } else {
      const { data, error } = await supabase.auth.signUp({ email, password });

      if (error) {
        setErrorMsg(error.message);
      } else if (data.user) {
        // Guardar en tabla Usuarios
        await supabase
          .from('Usuarios')
          .insert([{ Id: data.user.id, Nombre: nombreCompleto, Cargo: '', Area: '' }]);

        alert('Cuenta creada. Revisa tu correo para confirmar.');
        setIsLogin(true);
      }
    }
  };

  useEffect(() => {
    const token = localStorage.getItem('token') || sessionStorage.getItem('token');
    if (token) {
      supabase.auth.getUser(token).then(async ({ data: { user }, error }) => {
        if (error || !user) {
          localStorage.removeItem('token');
          sessionStorage.removeItem('token');
        } else {
          const { data: perfil } = await supabase
            .from('Usuarios')
            .select('Nombre')
            .eq('Id', user.id)
            .single();

          onAuthChange({
            ...user,
            nombre_completo: perfil?.Nombre || user.email,
          });
        }
      });
    }
  }, [onAuthChange]);

  return (
    <Box sx={{ height: '100vh', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
      <Grow in timeout={800}>
        <Paper elevation={6} sx={{ p: 4, width: '100%', maxWidth: 400 }}>
          <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <Avatar sx={{ width: 65, height: 65, m: 1, bgcolor: 'secondary.main' }}>
              <LockOutlinedIcon sx={{ width: 60, height: 60 }} />
            </Avatar>
            <Typography component="h1" variant="h5">
              {isLogin ? 'Iniciar sesión' : 'Registrarse'}
            </Typography>
            <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 1 }}>
              {!isLogin && (
                <TextField
                  margin="normal"
                  required
                  fullWidth
                  label="Nombre completo"
                  value={nombreCompleto}
                  onChange={(e) => setNombreCompleto(e.target.value)}
                />
              )}
              <TextField
                margin="normal"
                required
                fullWidth
                label="Correo electrónico"
                autoComplete="email"
                autoFocus
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
              <TextField
                margin="normal"
                required
                fullWidth
                label="Contraseña"
                type="password"
                autoComplete="current-password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
              <FormControlLabel
                control={
                  <Checkbox
                    checked={rememberMe}
                    onChange={(e) => setRememberMe(e.target.checked)}
                    color="primary"
                  />
                }
                label="Recordarme"
              />
              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
              >
                {isLogin ? 'LOGIN' : 'REGISTRARSE'}
              </Button>
              {errorMsg && (
                <Typography color="error" variant="body2" align="center">
                  {errorMsg}
                </Typography>
              )}
              <Grid container>
                <Grid item xs>
                  <Link href="#" variant="body2">
                    ¿Olvidaste tu contraseña?
                  </Link>
                </Grid>
                <Grid item>
                  <Link
                    href="#"
                    variant="body2"
                    onClick={() => setIsLogin(!isLogin)}
                  >
                    {isLogin ? '¿No tienes cuenta? Regístrate' : '¿Ya tienes cuenta? Inicia sesión'}
                  </Link>
                </Grid>
              </Grid>
            </Box>
          </Box>
        </Paper>
      </Grow>
    </Box>
  );
}

export default Auth;
