import React, { useEffect, useState } from 'react';
import {
  Table, TableBody, TableCell, TableContainer, TableHead, TableRow,
  Paper, Typography, CircularProgress, TextField, MenuItem, Box, Button,
  Snackbar, Alert
} from '@mui/material';

const OrdenesTrabajo = () => {
  const [ordenes, setOrdenes] = useState([]);
  const [loading, setLoading] = useState(true);
  const [busqueda, setBusqueda] = useState('');
  const [estadoFiltro, setEstadoFiltro] = useState('');
  const [maquinas, setMaquinas] = useState({});
  const [maquinasPedidos, setMaquinasPedidos] = useState({}); // Para contar los pedidos por máquina
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });

  const maquinasDisponibles = [
    'Maq03', 'Maq04', 'Maq05', 'Maq06', 'Maq07', 'Maq08',
    'Maq09', 'Maq10', 'Maq11', 'Maq12', 'Maq14', 'Maq15'
  ];

  useEffect(() => {
    fetchOrdenes();
  }, []);

  const [maquinasAsignadas, setMaquinasAsignadas] = useState({});

const fetchOrdenes = async () => {
  setLoading(true);
  try {
    const response = await fetch('http://localhost:4000/api/ordenes');
    const data = await response.json();
    setOrdenes(data);

    const maquinasIniciales = {};
    const pedidosPorMaquina = {};
    const asignadasIniciales = {};

    data.forEach((ot) => {
      if (ot.MaqId) {
        const sufijo = ot.MaqId.slice(-2);
        const maquina = `Maq${sufijo}`;
        maquinasIniciales[ot.OT] = maquina;

        if (!pedidosPorMaquina[maquina]) {
          pedidosPorMaquina[maquina] = 0;
        }
        pedidosPorMaquina[maquina]++;
        asignadasIniciales[ot.OT] = true; // ✅ marcar como asignada
      }
    });

    setMaquinas(maquinasIniciales);
    setMaquinasPedidos(pedidosPorMaquina);
    setMaquinasAsignadas(asignadasIniciales); // ✅ inicializar asignadas

  } catch (error) {
    console.error('Error al obtener órdenes:', error);
  } finally {
    setLoading(false);
  }
};

const handleMaquinaChange = (id, value) => {
  setMaquinas((prev) => ({ ...prev, [id]: value }));
};


const asignarMaquina = async (id) => {

  const maquinaSeleccionada = maquinas[id];
  if (!maquinaSeleccionada) return;

  const prefijo = ['03', '04', '07', '08', '12'].includes(maquinaSeleccionada.slice(-2))
    ? 'IMPDEB'
    : 'IMPENC';

  const maquinaFormateada = `${prefijo}${maquinaSeleccionada.slice(-2)}`;

  try {
    const token = localStorage.getItem('token');
    const response = await fetch(`http://localhost:4000/api/ordenes/${id}/asignar-maquina`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`,
      },
      body: JSON.stringify({ maquina: maquinaFormateada }),
    });

    const data = await response.json();

    if (!response.ok) {
      throw new Error(data.error || 'Error al asignar máquina');
    }

    setSnackbar({
      open: true,
      message: `Máquina ${maquinaFormateada} asignada correctamente a OT ${id}`,
      severity: 'success',
    });

    setMaquinasAsignadas((prev) => ({ ...prev, [id]: true })); // ✅ bloquear campo

    await fetchOrdenes(); // Refrescar datos

  } catch (error) {
    console.error(error);
    setSnackbar({
      open: true,
      message: 'Hubo un error al asignar la máquina',
      severity: 'error',
    });
  }
};


  const handleCloseSnackbar = () => {
    setSnackbar({ ...snackbar, open: false });
  };

  if (loading) return <CircularProgress />;

  const ordenesFiltradas = ordenes.filter((ot) => {
    const coincideBusqueda =
      ot.cliente.toLowerCase().includes(busqueda.toLowerCase()) ||
      ot.producto.toLowerCase().includes(busqueda.toLowerCase()) ||
      ot.pedido.toString().includes(busqueda);

    const coincideEstado = estadoFiltro ? ot.estado === estadoFiltro : true;

    return coincideBusqueda && coincideEstado;
  });

  return (
    <div style={{ padding: '20px' }}>
      <Typography variant="h6" gutterBottom sx={{ fontWeight: 'bold' }}>
        Órdenes de Trabajo
      </Typography>

      <Box display="flex" gap={2} mb={2}>
        <TextField
          label="Buscar"
          variant="outlined"
          size="small"
          value={busqueda}
          onChange={(e) => setBusqueda(e.target.value)}
          placeholder="Cliente, producto o pedido"
        />
        <TextField
          label="Estado"
          variant="outlined"
          size="small"
          select
          value={estadoFiltro}
          onChange={(e) => setEstadoFiltro(e.target.value)}
          sx={{ minWidth: 150 }}
        >
          <MenuItem value="">Todos</MenuItem>
          {Array.from(new Set(ordenes.map((ot) => ot.estado))).map((estado) => (
            <MenuItem key={estado} value={estado}>
              {estado}
            </MenuItem>
          ))}
        </TextField>
      </Box>

      <TableContainer component={Paper} elevation={0}>
        <Table size="small">
          <TableHead sx={{ backgroundColor: '#f0f0f0' }}>
            <TableRow>
              <TableCell><strong>OT</strong></TableCell>
              <TableCell><strong>Fecha OT</strong></TableCell>
              <TableCell><strong>Fecha Entrega</strong></TableCell>
              <TableCell><strong>Cliente</strong></TableCell>
              <TableCell><strong>Producto</strong></TableCell>
              <TableCell align="right"><strong>Cantidad</strong></TableCell>
              <TableCell><strong>Estado</strong></TableCell>
              <TableCell><strong>Pedido</strong></TableCell>
              <TableCell><strong>Máquina</strong></TableCell>
              <TableCell><strong>Pedidos</strong></TableCell> {/* Nueva columna */}
              <TableCell><strong>Cumplimiento</strong></TableCell> {/* Nueva columna */}
              <TableCell><strong>Acción</strong></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {ordenesFiltradas.map((ot, index) => (
              <TableRow
                key={ot.OT}
                sx={{
                  backgroundColor: index % 2 === 0 ? '#ffffff' : '#f9f9f9',
                }}
              >
                <TableCell>{ot.OT}</TableCell>
                <TableCell>{ot.fecha_ot}</TableCell>
                <TableCell>{ot.fecha_entrega}</TableCell>
                <TableCell>{ot.cliente}</TableCell>
                <TableCell>{ot.producto}</TableCell>
                <TableCell align="right">{ot.cantidad}</TableCell>
                <TableCell>{ot.estado}</TableCell>
                <TableCell>{ot.pedido}</TableCell>
                <TableCell>
                  <TextField
                    select
                    value={maquinas[ot.OT] || ''}
                    onChange={(e) => handleMaquinaChange(ot.OT, e.target.value)}
                    size="small"
                    sx={{ minWidth: 120 }}
                    disabled={maquinasAsignadas[ot.OT]} // ✅ solo se desactiva si ya fue asignada
                  >
                    {maquinasDisponibles.map((maquina) => (
                      <MenuItem key={maquina} value={maquina}>
                        {maquina}
                      </MenuItem>
                    ))}
                  </TextField>

                </TableCell>
                <TableCell>
                  {/* Mostramos los pedidos por máquina */}
                  {maquinasPedidos[maquinas[ot.OT]] || 0}
                </TableCell>
                <TableCell>
                  {/* Columna de cumplimiento (vacía por ahora) */}
                  {/* Puedes agregar más lógica cuando sea necesario */}
                </TableCell>
                <TableCell>
                   <Button
                    variant="contained"
                    color="primary"
                    onClick={() => asignarMaquina(ot.OT)}
                    disabled={maquinasAsignadas[ot.OT]} // ✅ solo se desactiva si ya fue asignada
                  >
                    {maquinasAsignadas[ot.OT] ? 'Máquina Asignada' : 'Asignar máquina'}
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Snackbar
        open={snackbar.open}
        autoHideDuration={4000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={handleCloseSnackbar} severity={snackbar.severity} sx={{ width: '100%' }}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </div>
  );
};

export default OrdenesTrabajo;
