import { useEffect, useState } from 'react';
import {
  Table, TableBody, TableCell, TableContainer, TableHead, TableRow,
  Paper, Typography, TextField, CircularProgress, Box
} from '@mui/material';

function Produccion() {
  const [ordenes, setOrdenes] = useState([]);
  const [filtro, setFiltro] = useState('');

  useEffect(() => {
    const fetchOrdenes = async () => {
      try {
        const res = await fetch('http://localhost:4000/api/ordenes');
        const data = await res.json();
        setOrdenes(data);
      } catch (error) {
        console.error('Error al obtener órdenes:', error);
      }
    };

    fetchOrdenes();
  }, []);

  const ordenesFiltradas = filtro
    ? ordenes.filter((o) =>
        o.estado.toLowerCase().includes(filtro.toLowerCase())
      )
    : ordenes;

  return (
    <div style={{ padding: '20px' }}>
      <Typography variant="h6" gutterBottom sx={{ fontWeight: 'bold' }}>
        Panel de Producción
      </Typography>

      <Box mb={2}>
        <TextField
          label="Filtrar por estado"
          variant="outlined"
          size="small"
          value={filtro}
          onChange={(e) => setFiltro(e.target.value)}
          placeholder="Ej: En proceso"
        />
      </Box>

      {ordenes.length === 0 ? (
        <CircularProgress />
      ) : (
        <TableContainer component={Paper} elevation={0}>
          <Table size="small">
            <TableHead sx={{ backgroundColor: '#f0f0f0' }}>
              <TableRow>
                <TableCell><strong>OT</strong></TableCell>
                <TableCell><strong>Cliente</strong></TableCell>
                <TableCell><strong>Producto</strong></TableCell>
                <TableCell><strong>Estado</strong></TableCell>
                <TableCell><strong>Fecha OT</strong></TableCell>
                <TableCell align="right"><strong>Cantidad</strong></TableCell>
                <TableCell align="right"><strong>Avance (%)</strong></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {ordenesFiltradas.map((ot, index) => (
                <TableRow
                  key={ot.OT}
                  sx={{
                    backgroundColor: index % 2 === 0 ? '#ffffff' : '#f9f9f9',
                  }}
                >
                  <TableCell>{ot.OT}</TableCell>
                  <TableCell>{ot.cliente}</TableCell>
                  <TableCell>{ot.producto}</TableCell>
                  <TableCell>{ot.estado}</TableCell>
                  <TableCell>{ot.fecha_ot}</TableCell>
                  <TableCell align="right">{ot.cantidad}</TableCell>
                  <TableCell align="right">{ot.porcentaje_avance}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}
    </div>
  );
}

export default Produccion;
