import React, { useEffect, useState } from 'react';
import {
  Table, TableBody, TableCell, TableContainer, TableHead, TableRow,
  Paper, TextField, Button, Snackbar, Alert, Typography, CircularProgress,
  Container, Box, Tooltip
} from '@mui/material';

function CrearOT() {
  const [pedidos, setPedidos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [cantidades, setCantidades] = useState({});
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });

  useEffect(() => {
    setLoading(true);
    fetch('http://localhost:4000/api/pedidos')
      .then(res => res.json())
      .then(data => {
        setPedidos(data);
        setLoading(false);
      })
      .catch(err => {
        console.error('Error cargando pedidos:', err);
        setLoading(false);
      });
  }, []);

  const handleCantidadChange = (pedNo, value) => {
    setCantidades(prev => ({ ...prev, [pedNo]: value }));
  };

  const generarOT = async (pedido) => {
    const token = localStorage.getItem('token') || sessionStorage.getItem('token');
    const cantidadProduccion = cantidades[pedido.PedNo];

    if (!cantidadProduccion || isNaN(cantidadProduccion) || cantidadProduccion <= 0) {
      setSnackbar({ open: true, message: 'Ingresa una cantidad válida.', severity: 'warning' });
      return;
    }

    const pedidoParaBackend = {
      PedNo: Number(pedido.PedNo),
      FechaEnt: pedido.FechaEnt,
      Cliente: pedido.Cliente,
      Producto: pedido.Producto,
      'Cant Pend Ped': Number(cantidadProduccion),
      CodCl: pedido.CodCl || '',
      CodProd: pedido.CodProd || '',
    };

    try {
      const res = await fetch('http://localhost:4000/api/crearOT', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`,
        },
        body: JSON.stringify(pedidoParaBackend),
      });

      const data = await res.json();

      if (res.ok) {
        setSnackbar({ open: true, message: `OT creada: ${data.OT}`, severity: 'success' });
        setCantidades(prev => ({ ...prev, [pedido.PedNo]: '' }));
      } else {
        setSnackbar({ open: true, message: `Error: ${data.error}`, severity: 'error' });
      }
    } catch (error) {
      console.error('Error en generarOT:', error);
      setSnackbar({ open: true, message: 'Error al crear OT.', severity: 'error' });
    }
  };

  const formatFecha = (fecha) => {
    try {
      return new Intl.DateTimeFormat('es-CO').format(new Date(fecha));
    } catch {
      return fecha;
    }
  };

  return (
    <Container maxWidth="lg">
      <Box mt={4}>
        <Typography variant="h5" gutterBottom>Pedidos pendientes de OT</Typography>
        {loading ? (
          <CircularProgress />
        ) : (
          <TableContainer component={Paper} sx={{ overflowX: 'auto' }}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Pedido</TableCell>
                  <TableCell>Fecha Entrega</TableCell>
                  <TableCell>Cliente</TableCell>
                  <TableCell>Producto</TableCell>
                  <TableCell>Cant. Pendiente</TableCell>
                  <TableCell>Cant. a Producir</TableCell>
                  <TableCell>Acción</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {Array.isArray(pedidos) && pedidos.length > 0 ? (
                  pedidos.map((pedido) => {
                    const cantidad = cantidades[pedido.PedNo] || '';
                    const errorCantidad = cantidad !== '' && (isNaN(cantidad) || cantidad <= 0);

                    return (
                      <TableRow key={pedido.PedNo}>
                        <TableCell>{pedido.PedNo}</TableCell>
                        <TableCell>{formatFecha(pedido.FechaEnt)}</TableCell>
                        <TableCell>{pedido.Cliente}</TableCell>
                        <TableCell>
                          <Tooltip title={pedido.Producto} arrow>
                            <span style={{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', display: 'inline-block', maxWidth: 200 }}>
                              {pedido.Producto}
                            </span>
                          </Tooltip>
                        </TableCell>
                        <TableCell>{pedido['Cant Pend Ped']}</TableCell>
                        <TableCell>
                          <TextField
                            type="number"
                            size="small"
                            variant="outlined"
                            value={cantidad}
                            error={errorCantidad}
                            helperText={errorCantidad ? 'Cantidad inválida' : ''}
                            onChange={(e) => handleCantidadChange(pedido.PedNo, e.target.value)}
                            inputProps={{ min: 1, max: pedido['Cant Pend Ped'] }}
                          />
                        </TableCell>
                        <TableCell>
                          <Button
                            variant="contained"
                            color="primary"
                            onClick={() => generarOT(pedido)}
                          >
                            Crear OT
                          </Button>
                        </TableCell>
                      </TableRow>
                    );
                  })
                ) : (
                  <TableRow>
                    <TableCell colSpan={7} align="center">No hay pedidos para mostrar</TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </TableContainer>
        )}

        <Snackbar
          open={snackbar.open}
          autoHideDuration={4000}
          onClose={() => setSnackbar({ ...snackbar, open: false })}
        >
          <Alert onClose={() => setSnackbar({ ...snackbar, open: false })} severity={snackbar.severity}>
            {snackbar.message}
          </Alert>
        </Snackbar>
      </Box>
    </Container>
  );
}

export default CrearOT;


